.card2 {
  /* height: 230px; */
  /* min-height: 200px; */
  width: 550px;
  min-width: 400px;
  margin: 0 20px;
  /* background-color: blue; */
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  position: relative;
  color: white;
}

.image:hover {
  cursor: pointer;
  transform: scale(1.05);
  transition: ease-in-out 0.2s;
}

h4:hover {
  color: #02c44d;
  cursor: pointer;
}

.h3_title {
  color: black;
}

.h3_title:hover {
  color: #02c44d;
  cursor: pointer;
}
