.card {
  height: 350px;
  min-height: 350px;
  width: 240px;
  margin: 0 20px;
  /* background-color: blue; */
  display: flex;
  flex-direction: row;
  position: relative;
  color: white;
}

.image:hover {
  cursor: pointer;
  transform: scale(1.1);
  transition: ease-in-out 0.2s;
}

h4:hover {
  color: #02c44d;
  cursor: pointer;
}
