.details-button {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
  border: 1px solid grey;
}

.details-button:hover {
  background-color: #02c44d;
  width: 130px;
  cursor: pointer;
  transition: ease-in-out 0.2s;
  border: none;
}

.details {
  position: absolute;
  color: white;
}
