.started-button {
  background-color: #02c44d;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  align-items: center;
  width: 150px;
  height: 40px;
  font-weight: 600;
  font-size: 16px;
}

.started-button:hover {
  background-color: white;
  color: #02c44d;
  transition: ease-in-out 0.2s;
  cursor: pointer;
}

.submit-button {
  background-color: #02c44d;
  color: white;
  display: flex;

  justify-content: center;
  align-items: center;
  width: 320px;
  height: 40px;
  font-weight: 600;
  font-size: 16px;
  border: none;
}
.com-Input {
  width: 320px;
  height: 40px;
  margin: 10px;
  border: 1px solid #00c40d3f;
  border-radius: 5px;
  padding-left: 10px;
  font-size: 16px;
  font-weight: 600;
  color: #000000;
}
.com-Input:hover {
  outline: none;
}
.com-Input:focus {
  outline: none;
}
.submit-button:hover {
  cursor: pointer;
}

.input {
  width: 100%;
  height: 40px;
  border: none;
}

.input:focus {
  outline: none;
}

.input-div {
  height: 40px;
  width: 300px;
  background-color: rgba(255, 255, 255, 0);
  padding-inline: 10px;
  display: grid;
  place-items: center;
}
.input-textarea {
  position: relative;
  padding-top: 50px;
  max-width: 300px;
}
p {
  color: gray;
  line-height: 20px;
  font-size: 13px;
}

.apply-button {
  margin-top: 20px;
  border: none;
  /* height: 40px; */
  padding-top: 10px;
  padding-bottom: 10px;
  /* width: 200px; */
  padding-inline: 20px;
  background-color: white;
  font-size: 13px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.apply-button:hover {
  cursor: pointer;
  background-color: black;
  color: white;
  transition: ease-in-out 0.2s;
}

.sub-footer {
  font-size: 38px;
  font-weight: 600;
  color: white;
}

.footer-buttons {
  margin: 0 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 40px;
  color: white;
  background-color: rgba(97, 97, 97, 0.322);
}
.footerfamily {
  font-family: Verdana, sans-serif;
}
.footer-buttons:hover {
  background-color: #02c44d;
  cursor: pointer;
  transition: ease-in-out 0.2s;
}
